import {PromptComponent} from "./prompt.component";
import {Component, ElementRef, Input} from "@angular/core";
import {FocusTrapFactory} from "@angular/cdk/a11y";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   selector: "vsan-info-prompt",
   templateUrl: "prompt.html",
   styleUrls: ["prompt.scss"],
})
export class InfoPrompt extends PromptComponent {

   /**
    * By default the info prompt dialog callback is not triggered on confirm, so that the target element
    * that opens the dialog can be focused on dialog close.
    * This property explicitly disables that behavior when set to true.
    * @type {boolean}
    */
   @Input() isCallbackTriggerEnabled: boolean = false;

   constructor(el: ElementRef, focusTrap: FocusTrapFactory) {
      super(el, focusTrap);
      this.confirmText = "vsan.common.close";
   }

   confirm(): void {
      if (this.isCallbackTriggerEnabled) {
         super.confirm();
      } else {
         VsanUiUtils.closeModalDialog();
      }
   }
}